import React, { Component } from 'react';
import { Table } from 'antd';

class TableStudentStudyAndExamAgain extends Component {
  constructor (props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'M sinh vin',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tn lp',
        dataIndex: 'classname',
        key: 'classname',
      },
      {
        title: 'K hiu',
        dataIndex: 'subjectId',
        key: 'subjectId',
      },
      {
        title: 'Tn hc phn',
        dataIndex: 'subjectLabel',
        key: 'subjectLabel',
      },
      {
        title: 'S tn ch',
        dataIndex: 'noOfCredit',
        key: 'noOfCredit',
      },
      {
        title: 'Nm hc',
        dataIndex: 'yearEdu',
        key: 'yearEdu',
      },
      {
        title: 'Hc k',
        dataIndex: 'semester',
        key: 'semester',
      },
      {
        title: 'Ln thi',
        dataIndex: 'noOfTest',
        key: 'noOfTest',
      },
      {
        title: 'im thi ln 1',
        dataIndex: 'firstGrade',
        key: 'firstGrade',
      },
      {
        title: 'im thi cao nht',
        dataIndex: 'maxGrade',
        key: 'maxGrade',
      },
    ];
  }

  onSelectChange = (selectedRowKeys, selectedRows) => {
    if (typeof this.props.handleTableRowSelection === 'function') {
      this.props.handleTableRowSelection(selectedRowKeys, selectedRows);
    }
  }

  render () {
    const { dataSource, selectedRowKeys } = this.props

    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    }
    return (
      <div>
        <Table
          bordered
          rowSelection={rowSelection}
          dataSource={dataSource}
          columns={this.columns}
          loading={this.props.loading}
          pagination={false}
        />
      </div>
    );
  }
}

export default TableStudentStudyAndExamAgain;



